
const Footer = ()=>{
    return(
        <footer className='mt-5'>
            <div style={{display:"flex" , justifyContent:"flex-end", borderTop:"solid 1px #D5D8DC",marginBottom:"5px"}}>
                <Title letter="T" color='#F7DC6F'></Title>
                <Title letter="r" color='#7DCEA0'></Title>
                <Title letter="a" color='#73C6B6'></Title>
                <Title letter="i" color='#76D7C4'></Title>
                <Title letter="n" color='#85C1E9'></Title>
                <Title letter="e" color='#7FB3D5'></Title>
                <Title letter="e" color='#BB8FCE'></Title>
                <Title letter="s" color='#C39BD3'></Title> 
            </div>
        </footer>
    )
}

const Title =( {letter , color } )=>{
    return <h3 style={{color:color}}>{letter}</h3>
}


export default Footer;