import React from 'react';
import { Col, Image, Row } from 'react-bootstrap';
import { FiEye , FiMapPin , FiChevronRight } from "react-icons/fi";

import { FaDocker , FaGit , FaNodeJs , FaNode , FaReact  } from "react-icons/fa";

import './style.css';
const CardHome =()=>{
    return(
        <article className='cardHome-container'>
            <Col>
                <Row className='cardHome-data-container'>
                        <Image fluid rounded src=''/>
                    <Col className='cardHome-data-info'>
                        <h5>Isaac Figueroa</h5>
                        <Row style={{textAlign:"right"}}>
                            <Col style={{marginRight:"15px"}}>
                                 <FaDocker style={{fontSize:"20px"}} /> <FaGit style={{fontSize:"20px"}} /> <FaNodeJs style={{fontSize:"20px"}} /> <FaNode style={{fontSize:"20px"}} /> <FaReact style={{fontSize:"20px"}} />
                            </Col>
                        </Row>
                        <p style={{marginTop:"9px"}}><FiMapPin/> Buenos Aires, Argentina</p>
                    </Col>
                </Row>
                <Row className='cardHome-footer-container'>
                    <Col>
                        <FiEye/>17
                    </Col>
                    <Col>
                        Ver perfil <FiChevronRight/>
                    </Col>
                </Row>
            </Col>
        </article>
    );
};



export default CardHome;


