import { useState } from "react";
import { Link, useHistory } from "react-router-dom";
import { gql, useMutation, useApolloClient } from '@apollo/client';
import { useUserDispatch, useUserState } from "../../context/userContext";

//styles
import './style.css';
//icons
import { FiMenu  , FiX } from "react-icons/fi";


const SIGNOUT = gql`
  mutation logout {
    logout
  }
`;

const Header =()=>{
    
    const [ isOpen , setIsOpen ] = useState(false);

    const [signout ] = useMutation(SIGNOUT);

    const userState = useUserState();

    const userDispatch = useUserDispatch();

    const client = useApolloClient();

    const history = useHistory();

    const handleOpen = (e)=>{
       return setIsOpen(!isOpen);
    }

    const handleClose=()=>{
        return setIsOpen(!isOpen);
    }

    return(
        <header>
            <div style={{display:"flex" , justifyContent:"center", borderBottom:"solid 1px #D5D8DC",marginBottom:"5px"}}>
                <Title letter="T" color='#F7DC6F'></Title>
                <Title letter="r" color='#7DCEA0'></Title>
                <Title letter="a" color='#73C6B6'></Title>
                <Title letter="i" color='#76D7C4'></Title>
                <Title letter="n" color='#85C1E9'></Title>
                <Title letter="e" color='#7FB3D5'></Title>
                <Title letter="e" color='#BB8FCE'></Title>
                <Title letter="s" color='#C39BD3'></Title> 
            </div>
            {
                !isOpen 
                ?
                    <FiMenu  onClick={handleOpen} style={{fontSize:"32px"}}/>
                :
                    <FiX  onClick={handleOpen}  style={{fontSize:"32px"}}/>
            }
            {
                isOpen &&
                <ul className="header-ul">
                    <li>
                        <Link 
                            onClick={()=>handleClose()} to="/"
                        >
                            Inicio
                        </Link>
                    </li>
                    {
                        userState.isAuthenticated ?
                        <>
                            <li>
                                <Link onClick={()=>handleClose()} to="/profile"
                                >
                                    Perfil
                                </Link> 
                            </li>
                            <li>
                                <Link 
                                    to="/singin"
                                    onClick={() => {
                                        signout().then(() => {
                                          localStorage.removeItem("id_token");
                                          client.clearStore();
                                          userDispatch({ type: "SIGN_OUT_SUCCESS" });
                                          history.push("/singin");
                                          handleClose();
                                        });
                                      }}
                                >
                                    Cerrar sesión
                                </Link>
                            </li>
                        </>
                        :
                        <>
                            <li>
                                <Link onClick={()=>handleClose()} to="/singin"
                                >
                                    Iniciar sesión
                                </Link>
                            </li>
                            <li>
                                <Link onClick={()=>handleClose()} to="/singup"
                                >
                                    Registrate
                                </Link>
                            </li>  
                        </>
                    }
                </ul>
            }
        </header>
    )
}



const Title =( {letter , color } )=>{
    return <h3 style={{color:color}}>{letter}</h3>
}

export default Header;