import { useMutation } from "@apollo/client";
import { gql } from "@apollo/client/core";
import { useState } from "react";
import { Button, Col, Form, Image, Row } from "react-bootstrap";
import { useForm } from "react-hook-form";
import {  GrFacebook , GrGoogle } from "react-icons/gr";
import { useHistory } from "react-router-dom";
import { useUserDispatch } from "../context/userContext";

const LOGIN = gql`
    mutation login($input:LoginInput){
       login(input:$input){
        token
        user{
            email
            active
            role
        }
       }
    }
`

const SingIn =()=>{  
    const [login  , {loading : loginLoading}] = useMutation(LOGIN);
    const { register, handleSubmit,  errors } = useForm();

    const [message , setMessage ] = useState( null );
    const history = useHistory();
    const userDispatch = useUserDispatch();

    const userRegister = data =>{
       login({
            variables:{
                input:{
                    email:data.email,
                    password:data.password
                }
            }
        }).then( data =>{
            userDispatch({ type: "LOGIN_SUCCESS" });
            localStorage.setItem("id_token", data.data.login.token);
            history.push('/');
        }).catch(errors=>{
            setMessage(errors.message)
        })
    }

    return(
       <>
            <Row style={{width:"100%", marginTop:"2rem", marginBottom:"1rem"}}>
                <Col xs={12} md={6}>
                    <h3 className='text-center'>Iniciar sesión</h3>
                     <Image fluid src="https://cdn.logo.com/hotlink-ok/logo-social.png"></Image> 
                    {
                        message &&
                        <h2>{message}</h2>
                    }
                </Col>
                <Col xs={12} md={6}>
                    <Form onSubmit={handleSubmit(userRegister)} >
                        <Form.Label column sm="2">
                        Email
                        </Form.Label>
                        <Col>
                            <Form.Control 
                                name="email" 
                                type="email"
                                autoComplete={"off"}
                                ref={register({
                                    required:"Email is required", 
                                    pattern: {
                                        value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                                        message: "invalid email address",
                                    },
                                })} 
                            />
                            {
                                errors.email &&
                                <p>{errors.email.message}</p>
                            }    
                        </Col>

                        <Form.Label column sm="2">
                        Contraseña
                        </Form.Label>
                        <Col>
                        <Form.Control 
                            name="password" 
                            autoComplete={"off"}
                            type="password"
                            ref={register({
                                required:"Password is required", 
                            })} 
                        />
                        {
                            errors.password &&
                            <p>{errors.password.message}</p>
                        }    
                    </Col>
                    <Col sm='10'>
                        <Button
                            variant="primary"
                            className='mt-3'
                            type="submit"
                            disabled={loginLoading ? true : false} 
                        >
                            {loginLoading ? "Ingresando" : "Ingresar"}
                        </Button>
                    </Col>
                    </Form>
                    
                </Col>
            </Row>
             <h6 className='text-center'>O iniciá con</h6>
            <Row style={{display:"flex", justifyContent:"center"}}>    
                <Col xs={2} sm={1} >
                    <Button variant="light">
                        <GrFacebook style={{color:"blue"}}/> 
                    </Button>
                </Col>
                <Col xs={2} sm={1}>
                        <Button  variant="light">
                        <GrGoogle/>  
                    </Button>
                </Col>
            </Row>
       </>
    )
}




export default SingIn;