import { useMutation } from "@apollo/client";
import { gql } from "@apollo/client/core";
import { useState } from "react";
import { Button, Col, Form, Image, Row } from "react-bootstrap";
import { useForm } from "react-hook-form";
import {  GrFacebook , GrGoogle } from "react-icons/gr";
// import { useHistory } from "react-router-dom";
// import { useUserDispatch } from "../context/userContext";

const CREATE_USER = gql`
    mutation registerUser($input:RegisterUserInput){
       registerUser(input:$input)
    }
`

const SingUp =()=>{  
    const [registerUser , {loading : loginLoading} ] = useMutation(CREATE_USER);
    const { register, handleSubmit,  errors } = useForm();

    const [message , setMessage ] = useState( null );
    const userRegister = data =>{
       registerUser({
            variables:{
                input:{
                    name:data.name,
                    email:data.email,
                    password:data.password
                }
            }
        }).then( data =>{
            let registerUser = data.data.registerUser;
            console.log(registerUser)
            if(registerUser === false){
               setMessage("Email en uso");
            }else{
                setMessage("Registrado correctamente"); 
            }
        }).catch(errors=>{
            console.log(errors)
        })
    }
    return(
       <>
            <Row style={{width:"100%", marginTop:"2rem", marginBottom:"1rem"}}>
                <Col xs={12} md={6}>
                    <h3 className='text-center'>Registrate</h3>
                   <Image fluid src="https://cdn.logo.com/hotlink-ok/logo-social.png"></Image> 
                    {
                        message &&
                        <h2>{message}</h2>
                    }
                </Col>
                <Col xs={12} md={6}>
                    <Form onSubmit={handleSubmit(userRegister)} >
                    <Form.Label column sm="2">
                        Nombre
                        </Form.Label>
                        <Col>
                            <Form.Control 
                                name="name" 
                                type="Name"
                                autoComplete={"off"}
                                ref={register({
                                    required:"Name is required", 
                                })} 
                            />
                            {
                                errors.name &&
                                <p>{errors.name.message}</p>
                            }    
                        </Col>

                        <Form.Label column sm="2">
                        Email
                        </Form.Label>
                        <Col>
                            <Form.Control 
                                name="email" 
                                type="email"
                                autoComplete={"off"}
                                ref={register({
                                    required:"Email is required", 
                                    pattern: {
                                        value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                                        message: "invalid email address",
                                    },
                                })} 
                            />
                            {
                                errors.email &&
                                <p>{errors.email.message}</p>
                            }    
                        </Col>

                        <Form.Label column sm="2">
                        Contraseña
                        </Form.Label>
                        <Col>
                        <Form.Control 
                            name="password" 
                            autoComplete={"off"}
                            type="password"
                            ref={register({
                                required:"Password is required", 
                            })} 
                        />
                        {
                            errors.password &&
                            <p>{errors.password.message}</p>
                        }    
                    </Col>
                    <Col sm='10'>
                        <Button
                            variant="primary"
                            className='mt-3'
                            type="submit"
                            disabled={loginLoading ? true : false} 
                        >
                            {loginLoading ? "Registrando" : "Registrarme"}
                        </Button>
                    </Col>
                    </Form>
                    
                </Col>
            </Row>
             <h6 className='text-center'>O iniciá con</h6>
            <Row style={{display:"flex", justifyContent:"center"}}>    
                <Col xs={2} sm={1} >
                    <Button variant="light">
                        <GrFacebook style={{color:"blue"}}/> 
                    </Button>
                </Col>
                <Col xs={2} sm={1}>
                        <Button  variant="light">
                        <GrGoogle/>  
                    </Button>
                </Col>
            </Row>
       </>
    )
}




export default SingUp;